/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1a1a1a;
  color: #ffffff;
  overflow-x: hidden;
}

.App {
  display: flex;
  min-height: 100vh;
  background-color: #1a1a1a;
}

/* Sidebar Styles */
.sidebar {
  width: 280px;
  background-color: #2d2d2d;
  border-right: 1px solid #404040;
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 1000;
}

.sidebar-header {
  padding: 20px;
  border-bottom: 1px solid #404040;
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Hamburger menu removed */

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
}

.logo-icon {
  color: #ffd700;
  font-size: 20px;
}

.logo-text {
  color: #ffd700;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1px;
}

.sidebar-nav {
  flex: 1;
  padding: 20px 0;
}

.nav-item {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  color: #ffffff;
  text-decoration: none;
  transition: all 0.2s;
  cursor: pointer;
  border-left: 3px solid transparent;
}

.nav-item:hover {
  background-color: #404040;
  color: #ffd700;
}

.nav-item.active {
  background-color: #404040;
  border-left-color: #ffd700;
  color: #ffd700;
}

.nav-icon {
  font-size: 16px;
  margin-right: 12px;
  width: 20px;
  text-align: center;
}

.nav-text {
  flex: 1;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.5px;
}

.nav-arrow {
  font-size: 16px;
  opacity: 0.7;
}

.sidebar-footer {
  padding: 20px;
  border-top: 1px solid #404040;
}

.ai-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.ai-icon-image {
  width: 48px;
  height: 48px;
  object-fit: contain;
  border-radius: 8px;
}

.ai-icon-fallback {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ai-icon {
  width: 24px;
  height: 24px;
  background-color: #ffd700;
  color: #1a1a1a;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: bold;
}

/* Main Content Area */
.main-content {
  flex: 1;
  margin-left: 280px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header Styles */
.main-header {
  background-color: #2d2d2d;
  border-bottom: 1px solid #404040;
  padding: 0 30px;
  height: 60px;
  display: flex;
  align-items: center;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.page-title {
  font-size: 20px;
  font-weight: 600;
  color: #ffffff;
  letter-spacing: 0.5px;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.welcome-text {
  color: #ffffff;
  font-size: 14px;
}

.logout-btn {
  background-color: #dc3545;
  color: #ffffff;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.logout-btn:hover {
  background-color: #c82333;
}

/* Content Area */
.content-area {
  flex: 1;
  padding: 30px;
  background-color: #1a1a1a;
}

/* Dashboard Cards */
.dashboard-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
  max-width: 800px;
}

.dashboard-card {
  background-color: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
}

.card-title {
  font-size: 12px;
  color: #cccccc;
  margin-bottom: 8px;
  letter-spacing: 0.5px;
}

.card-value {
  font-size: 32px;
  font-weight: bold;
  color: #ffd700;
}

/* Form and Map Container */
.form-and-map-container {
  display: grid;
  grid-template-columns: 1fr 600px;
  gap: 30px;
  margin-bottom: 30px;
}

.reporting-form {
  background-color: #2d2d2d;
  padding: 30px;
  border-radius: 8px;
  border: 1px solid #404040;
}

.map-container {
  background-color: #2d2d2d;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid #404040;
}

.map-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.map-header h3 {
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
}

.map-controls {
  display: flex;
  gap: 8px;
}

.map-btn {
  background-color: #404040;
  color: #ffffff;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.2s;
}

.map-btn:hover {
  background-color: #ffd700;
  color: #1a1a1a;
}

.map-instructions {
  margin-bottom: 15px;
}

.map-instructions small {
  color: #cccccc;
  font-size: 12px;
}

.map-tile {
  border-radius: 8px;
  overflow: hidden;
}

/* Form Styles */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 8px;
}

.form-input, .form-textarea, .search-input {
  width: 100%;
  padding: 12px;
  background-color: #404040;
  border: 1px solid #555555;
  border-radius: 4px;
  color: #ffffff;
  font-size: 14px;
  transition: border-color 0.2s;
}

.form-input:focus, .form-textarea:focus, .search-input:focus {
  outline: none;
  border-color: #ffd700;
}

.form-input::placeholder, .search-input::placeholder {
  color: #999999;
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
}

.time-input-container {
  display: flex;
  gap: 8px;
  align-items: center;
}

.time-input {
  flex: 1;
}

.current-time-btn {
  background-color: #404040;
  color: #ffffff;
  border: 1px solid #555555;
  padding: 12px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: background-color 0.2s;
}

.current-time-btn:hover {
  background-color: #ffd700;
  color: #1a1a1a;
}

.location-search-container {
  display: flex;
  gap: 8px;
}

.location-input {
  flex: 1;
}

.location-search-btn {
  background-color: #ffd700;
  color: #1a1a1a;
  border: none;
  padding: 12px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  transition: background-color 0.2s;
}

.location-search-btn:hover {
  background-color: #ffed4e;
}

.location-search-btn:disabled {
  background-color: #666666;
  color: #999999;
  cursor: not-allowed;
}

.coordinates-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.coordinates-info {
  grid-column: 1 / -1;
  margin-top: 10px;
}

.coordinates-info small {
  color: #cccccc;
  font-size: 12px;
}

.loading-indicator {
  margin-top: 8px;
}

.loading-indicator small {
  color: #ffd700;
  font-size: 12px;
}

.form-help-text {
  color: #cccccc;
  font-size: 12px;
  margin-top: 8px;
  line-height: 1.4;
}

.button-group {
  display: flex;
  gap: 15px;
  margin-top: 30px;
}

.submit-btn, .save-btn {
  flex: 1;
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.submit-btn {
  background-color: #ffd700;
  color: #1a1a1a;
}

.submit-btn:hover {
  background-color: #ffed4e;
}

.save-btn {
  background-color: #404040;
  color: #ffffff;
  border: 1px solid #555555;
}

.save-btn:hover {
  background-color: #555555;
}

.clear-search-btn {
  background-color: #666666;
  color: #ffffff;
  border: 1px solid #777777;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.clear-search-btn:hover {
  background-color: #777777;
}

/* Message Styles */
.message {
  background-color: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 4px;
  padding: 15px;
  margin: 20px 0;
  color: #ffffff;
  font-size: 14px;
}

/* Advanced Search */
.advanced-search {
  background-color: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0;
}

.advanced-search h3 {
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 15px;
}

.advanced-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 15px;
  margin-bottom: 15px;
}

.advanced-grid label {
  color: #ffffff;
  font-size: 12px;
  font-weight: 500;
  margin-bottom: 5px;
  display: block;
}

/* Sightings List */
.sightings-list {
  background-color: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 20px;
}

.sightings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.sightings-header h3 {
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
}

.search-container {
  display: flex;
  gap: 10px;
  align-items: center;
}

.search-results-info {
  margin-bottom: 15px;
}

.search-results-info small {
  color: #cccccc;
  font-size: 12px;
}

.sightings-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.sighting-card {
  background-color: #404040;
  border: 1px solid #555555;
  border-radius: 8px;
  padding: 20px;
}

.sighting-card h4 {
  color: #ffd700;
  font-size: 16px;
  margin-bottom: 10px;
}

.sighting-card p {
  color: #ffffff;
  font-size: 14px;
  margin-bottom: 8px;
  line-height: 1.4;
}

.sighting-card strong {
  color: #ffd700;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.modal-content {
  background-color: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #404040;
}

.modal-header h2 {
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}

.modal-close {
  background: none;
  border: none;
  color: #ffffff;
  font-size: 24px;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.modal-close:hover {
  background-color: #404040;
}

.modal-body {
  padding: 20px;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding: 20px;
  border-top: 1px solid #404040;
}

.save-form-btn {
  background-color: #ffc107;
  color: #1a1a1a;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.save-form-btn:hover:not(:disabled) {
  background-color: #e0a800;
}

.save-form-btn:disabled {
  background-color: #6c757d;
  color: #ffffff;
  cursor: not-allowed;
}

/* Admin Page Styles */
.admin-page {
  min-height: 100vh;
  background-color: #000000;
  color: #E0E0E0;
}

.admin-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.admin-tabs {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  border-bottom: 1px solid #404040;
}

.admin-tab {
  background: none;
  border: none;
  color: #E0E0E0;
  padding: 12px 20px;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.2s;
}

.admin-tab:hover {
  color: #ffd700;
  background-color: #2d2d2d;
}

.admin-tab.active {
  color: #ffd700;
  border-bottom-color: #ffd700;
  background-color: #2d2d2d;
}

.tab-icon {
  font-size: 16px;
}

.admin-content {
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 30px;
  border: 1px solid #404040;
}

.admin-section {
  margin-bottom: 40px;
}

.admin-section h3 {
  color: #ffd700;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 600;
}

.admin-form {
  background-color: #2d2d2d;
  padding: 20px;
  border-radius: 6px;
  border: 1px solid #404040;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  color: #E0E0E0;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 500;
}

.users-table {
  background-color: #2d2d2d;
  border-radius: 6px;
  border: 1px solid #404040;
  overflow: hidden;
}

.table-header {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
  gap: 20px;
  padding: 15px 20px;
  background-color: #404040;
  color: #ffd700;
  font-weight: 600;
  font-size: 14px;
}

.table-row {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
  gap: 20px;
  padding: 15px 20px;
  border-bottom: 1px solid #404040;
  align-items: center;
}

.table-row:last-child {
  border-bottom: none;
}

.role-badge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 4px;
  color: white;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  min-width: 60px;
}

.action-buttons {
  display: flex;
  gap: 8px;
}

.action-btn {
  background: none;
  border: none;
  color: #E0E0E0;
  cursor: pointer;
  padding: 6px;
  border-radius: 4px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.action-btn.edit:hover {
  background-color: #ffc107;
  color: #1a1a1a;
}

.action-btn.delete:hover {
  background-color: #dc3545;
  color: white;
}

.tables-list {
  display: grid;
  gap: 20px;
}

.table-card {
  background-color: #2d2d2d;
  border-radius: 6px;
  border: 1px solid #404040;
  overflow: hidden;
}

.table-card .table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #404040;
  color: #ffd700;
}

.table-card .table-header h4 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.table-stats {
  display: flex;
  gap: 15px;
  font-size: 12px;
  color: #E0E0E0;
}

.table-columns {
  padding: 20px;
}

.table-columns h5 {
  color: #ffd700;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: 600;
}

.columns-list {
  display: grid;
  gap: 8px;
}

.column-item {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 15px;
  padding: 8px 12px;
  background-color: #1a1a1a;
  border-radius: 4px;
  border: 1px solid #404040;
  font-size: 13px;
}

.column-name {
  color: #ffd700;
  font-weight: 600;
}

.column-type {
  color: #E0E0E0;
  font-family: monospace;
}

.column-nullable {
  font-size: 11px;
  font-weight: 600;
  text-align: center;
  padding: 2px 6px;
  border-radius: 3px;
}

.column-nullable.nullable {
  background-color: #28a745;
  color: white;
}

.column-nullable.not-null {
  background-color: #dc3545;
  color: white;
}

/* Analysis Page Styles */
.analysis-page {
  min-height: 100vh;
  background-color: #000000;
  color: #E0E0E0;
}

.analysis-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.analysis-header {
  text-align: center;
  margin-bottom: 50px;
}

.analysis-icon {
  font-size: 4rem;
  color: #ffd700;
  margin-bottom: 20px;
}

.analysis-header h1 {
  color: #ffd700;
  font-size: 2.5rem;
  margin-bottom: 15px;
  font-weight: 600;
}

.analysis-subtitle {
  color: #E0E0E0;
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.analysis-content {
  display: grid;
  gap: 40px;
}

.coming-soon-card {
  background: linear-gradient(135deg, #2d2d2d 0%, #404040 100%);
  border: 1px solid #ffd700;
  border-radius: 12px;
  padding: 40px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(255, 215, 0, 0.1);
}

.coming-soon-icon {
  font-size: 3rem;
  color: #ffd700;
  margin-bottom: 20px;
}

.coming-soon-card h2 {
  color: #ffd700;
  font-size: 2rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.coming-soon-card p {
  color: #E0E0E0;
  font-size: 1.1rem;
  line-height: 1.7;
  max-width: 800px;
  margin: 0 auto;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.feature-card {
  background-color: #1a1a1a;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 30px;
  text-align: center;
  transition: all 0.3s ease;
}

.feature-card:hover {
  border-color: #ffd700;
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.15);
}

.feature-icon {
  font-size: 2.5rem;
  color: #ffd700;
  margin-bottom: 20px;
}

.feature-card h3 {
  color: #ffd700;
  font-size: 1.3rem;
  margin-bottom: 15px;
  font-weight: 600;
}

.feature-card p {
  color: #E0E0E0;
  line-height: 1.6;
  font-size: 0.95rem;
}

.development-status {
  background-color: #1a1a1a;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 30px;
  margin-top: 40px;
}

.development-status h3 {
  color: #ffd700;
  font-size: 1.5rem;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 600;
}

.status-timeline {
  display: grid;
  gap: 25px;
  max-width: 800px;
  margin: 0 auto;
}

.status-item {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.status-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-top: 5px;
  flex-shrink: 0;
}

.status-item.completed .status-dot {
  background-color: #28a745;
  box-shadow: 0 0 10px rgba(40, 167, 69, 0.5);
}

.status-item.in-progress .status-dot {
  background-color: #ffc107;
  box-shadow: 0 0 10px rgba(255, 193, 7, 0.5);
  animation: pulse 2s infinite;
}

.status-item.pending .status-dot {
  background-color: #6c757d;
  box-shadow: 0 0 10px rgba(108, 117, 125, 0.5);
}

.status-content h4 {
  color: #ffd700;
  font-size: 1.1rem;
  margin-bottom: 8px;
  font-weight: 600;
}

.status-content p {
  color: #E0E0E0;
  font-size: 0.9rem;
  line-height: 1.5;
}

.contact-info {
  background-color: #1a1a1a;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 30px;
  text-align: center;
  margin-top: 40px;
}

.contact-info h3 {
  color: #ffd700;
  font-size: 1.3rem;
  margin-bottom: 15px;
  font-weight: 600;
}

.contact-info p {
  color: #E0E0E0;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 10px rgba(255, 193, 7, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(255, 193, 7, 0.8);
  }
  100% {
    box-shadow: 0 0 10px rgba(255, 193, 7, 0.5);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .form-and-map-container {
    grid-template-columns: 1fr;
  }
  
  .map-container {
    order: -1;
  }
}

@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
  
  .sidebar.open {
    transform: translateX(0);
  }
  
  .main-content {
    margin-left: 0;
  }
  
  .header-content {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }
  
  .content-area {
    padding: 20px;
  }
  
  .coordinates-group {
    grid-template-columns: 1fr;
  }
  
  .sightings-grid {
    grid-template-columns: 1fr;
  }
}